* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #f4f6f7;
  padding: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box, .country-list {
  width: 100%;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

#search-box {
  font: inherit;
  color: #4d4d4d;
  background-color: ffffff;
  z-index: 999;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding: 8px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), outline .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
  box-shadow: 5px 0 40px rgba(0, 0, 0, .1);
}

#search-box::-ms-input-placeholder {
  opacity: .5;
}

#search-box::placeholder {
  opacity: .5;
}

#search-box:hover, #search-box:focus, #search-box:active {
  outline: 1px solid rgba(12, 60, 100, .267);
  box-shadow: 0 0 20px -2px rgba(15, 60, 119, .827);
}

.country-list {
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 30px;
  padding: 8px;
  list-style-type: none;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 0 40px -5px rgba(0, 0, 0, .1);
}

.country-list:hover, .country-list:focus {
  box-shadow: 0 5px 40px -5px rgba(96, 112, 255, .39);
}

.country-list-item {
  display: flex;
}

.country-list-item:not(:last-child) {
  margin-bottom: 8px;
}

.country-list-item > img {
  margin-right: 5px;
}

.country-info {
  max-width: 500px;
  background-color: #fff;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 0 20px -5px #696969;
}

.country-info:hover, .country-info:focus {
  box-shadow: 0 0 30px -5px #2c2c2c;
}

.country-info > img {
  width: 100%;
  height: auto;
  margin-bottom: 8px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.country-info-title {
  color: #202020;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.5;
}

.country-info-text {
  color: #302f2f;
  font-size: 20px;
  line-height: 1.25;
}

.country-info-text span {
  color: #385791;
}

.country-info-content:not(:last-child) {
  margin-bottom: 8px;
}

